import {
	mapState, mapMutations
} from 'vuex';
import api from '@/common/vmeitime-http/'
import json from '@/Json'
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: true, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			haslogin: false,
			cartTotal: {},
			goodsList: [],
			agentId: 7813
		}
	},
	onLoad(){
		this.loadData();
		this.loadtopData()
	},
	onPullDownRefresh() {
		this.loadData(true);
		this.loadtopData()
	},
	computed:{
		...mapState(['hasLogin'])
	},
	methods: {
		...mapMutations(['login']),
		onShareAppMessage(res) {
			json.sharebtn(res)
		},
		//详情页
		navToDetailPage(id) {
			uni.navigateTo({
				url: `/pages/product/product?goodsId=${id}`
			})
		},
		getUserInfo: function() {
			let that = this;










































			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					uni.showLoading({title: '正在登录'})
					let code = loginRes.code;
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						lang:'zh_CN',
						success: function(infoRes) {
							let userInfo = infoRes.userInfo;
							var options = {
								code: code,
								userInfo: userInfo
							} 
							that.login(userInfo)
							setTimeout(function() {
								uni.hideLoading()
							}, 1000);
							that.gologin(options)
						},
						fail: function() {
							uni.navigateTo({
								url: '/pages/public/login'
							})
							uni.hideLoading()
						}
					})
				},
				fail: function(loginRes) {
					if (uni.getStorageSync('haslogin') == '') {
						that.haslogin = false
					}
					uni.navigateTo({
						url: '/pages/public/login'
					})
					uni.hideLoading()
				}
			})

		},
		gologin(options, data) {
			let that = this;
			let loginurl = '/auth/login_by_weixin';
			api.httppost(loginurl, options).then((res) => {
				if (res.data.errno == 0) {
					that.haslogin = true
					uni.setStorageSync('token', res.data.data.token);
					uni.setStorageSync('haslogin', true);
					that.loadData()
				}
				uni.hideLoading()
			}).catch((err) => {
				uni.showToast({title: res.data.errmsg, icon: 'none'})
				uni.hideLoading()
			})
		},
		//请求数据
		async loadData(flag){
			uni.showLoading({title: '正在加载'})
			let carturl = '/cart/index'
			api.httpget(carturl).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					this.cartList = respons.data.cartList
					this.cartTotal = respons.data.cartTotal
					this.cartList.forEach(item=>{
						if(!item.checked)  this.allChecked = false
					})
				} else {
					uni.showToast({title: respons.errmsg, icon: 'none'})
				}
				if(flag) uni.stopPullDownRefresh();
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
				if(flag) uni.stopPullDownRefresh();
			})
		},
		async loadtopData(flag) {
			uni.showLoading({title: '正在加载'})
			let homeurl = '/home/index';
			api.httpget(homeurl).then((res) => {
				let respons = res.data;
				let resp = respons.data;
				if(respons.errno == 0) {
					this.goodsList = resp.hotGoodsList;
					uni.setStorageSync('goodsList', this.goodsList)
				}
				if(flag) uni.stopPullDownRefresh()
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
				if(flag) uni.stopPullDownRefresh()
			})
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		 //选中状态处理
		check(type, index,item){
			let arr = []
			let data = {}
			if(type === 'item'){
				this.cartList[index].checked = !this.cartList[index].checked;
				data = {isChecked: item.checked?1:0, productIds: [item.productId] }
			} else {
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item=>{
					item.checked = checked;
					arr.push(item.productId)
				})
				this.allChecked = checked;
				data.productIds = arr
				data = {isChecked: this.allChecked?1:0, productIds: arr}
			}
			this.calcTotal()
			let checkedurl = '/cart/checked';
			api.httppost(checkedurl, data).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					this.loadData()
				}
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
			})
		},
		//数量
		numberChange(item,data){
			uni.showLoading({title: '正在加载'})
			let updateurl = '/cart/update';
			api.httppost(updateurl,{goodsId: item.goodsId, id:item.id, number: item.number, productId: item.productId}).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					this.loadData()
				}
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
			})
		},
		//删除
		deleteCartItem(id){
			this.deletepro([id])
		},
		deletepro (arr) {
			uni.showLoading({title: '正在加载'})
			let deleteurl = '/cart/delete';
			api.httppost(deleteurl,{productIds: arr}).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					this.cartList = respons.data.cartList;
				}
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
			})
		},
		//清空
		clearCart(){
			let arr = []
			uni.showModal({
				content: '清空购物车？',
				success: (e)=>{
					if(e.confirm){
						this.cartList.forEach(item => {
							arr.push(item.id)
						})
						this.deletepro(arr);
					}
				}
			})
		},
		//计算总价
		calcTotal(){
			let list = this.cartList;
			if(list.length === 0){
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item=>{
				if(item.checked === true){
					total += item.price * item.number;
				}else if(checked === true){
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		cutNumber: function(item) {
		  item.number = (item.number - 1 > 1) ? item.number - 1 : 1
		  this.numberChange(item)
	    },
		  addNumber: function(item) {
			  item.number = item.number + 1
			  this.numberChange(item)
		  },
		//创建订单
		createOrder(){
			if (this.cartTotal.checkedGoodsAmount <= 0) return false;
			uni.setStorageSync('cartId', 0)
			uni.navigateTo({
				url: `/pages/order/createOrder`
			})
		}
	}
}
